html {
    margin: 0;
    padding: 0;
    font-family: 'Courier New', Courier, monospace;
    scroll-behavior: smooth;
}



#head-wrapper {
    display: none;
}

#header-block {
    width: 100%;
    height: 100vh;
    position: fixed;
    display: grid;
    align-items: center;
    justify-items: center;
}



@keyframes cursor_blink {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

#cursor {
    animation-name: cursor_blink;
    animation-iteration-count: infinite;
    animation-duration: 1s;
}

img {
    -webkit-user-drag: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    
}

::-webkit-scrollbar {
    width: 9px;
    background-color: #212121;
}


/* Track */

  
/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(60, 179, 216); 
    border-radius: 10px;
  }

@media (max-width: 700px) {
    #header-block {
        padding: 20px;
    }
}